function hidePhoneNum(num) {
	return num.replace(/(.{3}).*(.{2})/, '$1******$2')
}
exports.hidePhoneNum = hidePhoneNum

function hideBankAccount(num) {
	return num.replace(/(.{4}).*(.{4})/, '$1******$2')
}
exports.hideBankAccount = hideBankAccount

